@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15px);
    }
}

.ServiceCV-container{
    font-family: "GmarketSansMedium";
    width: 100%;
    .down-btn{
        color: #FFBF00;
        position: absolute;
        bottom: 3%;
        transform: translateX(-50%);
        cursor: pointer;
        animation: bounce 2s infinite;
        svg{
            font-size: 40px;
        }
    }
    .CV-content-one{
        height: 100vh;
        width: 100%;
        background-image: url(../../../public/images/service-back.png);
        background-size: cover;
        background-position: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .CV-title-one{
            width: 100%;
            margin-bottom: 50px;
            opacity: 0;
            transform: translateY(20%);
            transition: all 1s ease-in-out;
            &.in-view{
                opacity: 1;
                transform: translateY(0);
            }
            h1{
                font-size: 2.5rem;
                color: #333;
                text-align: center;
                position: relative;
                margin: 0 auto;
                margin-bottom: 100px;
                backdrop-filter: blur(0.5rem);
                &::after{
                    content: "";
                    width: 200px;
                    height: 7px;
                    background-color: #FFBF00;
                    position: absolute;
                    bottom: -20px;
                    right: 50%;
                    transform: translateX(50%);
                }
            }
            h5 {
                text-align: center;
                margin: 0 auto;
                color: #333;
                font-size: 1.3rem;
            }
        }
        .CV-description-one{
            width: 100%;
            height: 50vh;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;   
            opacity: 0;
            transform: translateY(20%);
            transition: all 1s ease-in-out;
            transition-delay: 1s;
            &.in-view{
                opacity: 1;
                transform: translateY(0);
            }
            .CV-description-one-image{
                background-image: url(../../../public/images/viral-one.png);
                background-size: cover;
                background-position: center;
                width: 650px;
                height: 450px;
                position: absolute;
                left: 40%;
            }
            .CV-description-one-content{
                position: absolute;
                height: 450px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                text-align: left;
                line-height: 2;
                right: 45%;
                h4{
                    font-size: 1.5rem;
                    color: #333;
                    margin-bottom: 20px;
                }
                p{
                    color: #333;
                }
            }
        }
    }
    .CV-content-why{
        width: 100%;
        padding-top: 17vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        padding-bottom: 10vh;
        .CV-why-title{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 80px;
            h1{
                font-size: 2.5rem;
                color: #333;
                text-align: center;
                position: relative;
                margin-bottom: 30px;
                opacity: 0;
                transform: translateY(50px);
                transition: all .5s ease-in-out;
                &.in-view{
                    opacity: 1;
                    transform: translateY(0);
                }
                &::after {
                    content: "";
                    width: 100px;
                    height: 7px;
                    background-color: #FFBF00;
                    position: absolute;
                    bottom: -10px;
                    right: 50%;
                    transform: translateX(50%);
                }
            }
        }
        .CV-why-list{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .CV-why-item{
                width: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 100px;
                border-radius: 10px;
                box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
                padding: 20px;
                opacity: 0;
                transform: translateY(50px);
                transition: all 0.5s ease-in-out;
                transition-delay: 1s;
                &.in-view{
                    opacity: 1;
                    transform: translateY(0);
                }
                &:hover{
                    transform: scale(1.05);
                }
                .why-item-img{
                    width: 200px;
                    height: 130px;
                    margin-right: 30px;
                    border-radius: 10px;
                }
                &:nth-child(1){
                    .why-item-img{
                        background-image: url(../../../public/images/viralwhy-1.jpg);
                        background-size: cover;
                        background-position: center;
                    }
                }
                &:nth-child(2){
                    .why-item-img{
                        background-image: url(../../../public/images/viralwhy-2.jpg);
                        background-size: cover;
                        background-position: center;
                    }
                }
                &:nth-child(3){
                    .why-item-img{
                        background-image: url(../../../public/images/viralwhy-3.jpg);
                        background-size: cover;
                        background-position: center;
                    }
                }
                &:nth-child(4){
                    .why-item-img{
                        background-image: url(../../../public/images/viralwhy-4.jpg);
                        background-size: cover;
                        background-position: center;
                    }
                }
                .why-item-description{
                    width: calc(100% - 250px);
                    h3{
                        font-size: 1.5rem;
                        margin-bottom: 10px;
                        text-align: left;
                        padding-left: 10px;
                        border-left: 5px solid #FFBF00;
                    }
                    p{
                        color: #333;
                        text-align: left;
                        line-height: 1.5;
                    }
                }
            }
        }
    }
    .CV-content-two{
        height: 100vh;
        width: 100%;
        padding-top: 17vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #F5F7F8;
        position: relative;
        .CV-title-two{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 80px;
            opacity: 0;
            transform: translateY(20%);
            transition: all 1s ease-in-out;
            &.in-view{
                opacity: 1;
                transform: translateY(0);
            }
            h1 {
                font-size: 2.5rem;
                color: #333;
                text-align: center;
                width: 70%;
                position: relative;
                margin-bottom: 30px;
                &::after {
                    content: "";
                    width: 100px;
                    height: 7px;
                    background-color: #FFBF00;
                    position: absolute;
                    bottom: -10px;
                    right: 50%;
                    transform: translateX(50%);
                }
            }
        }
        .CV-description-two{
            width: 70%;
            display: flex;
            justify-content: space-around;
            opacity: 0;
            transform: translateY(20%);
            transition: all 1s ease-in-out;
            transition-delay: 1s;
            &.in-view{
                opacity: 1;
                transform: translateY(0);
            }
            .specialBox{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                padding: 20px;
                width: 20%;
                .specialTitle{
                    width: 150px;
                    height: 150px;
                    margin-bottom: 50px;
                    padding: 20px;
                    border-radius: 50%;
                    background-color: #FFF;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    svg{
                        font-size: 50px;
                        margin-bottom: 10px;
                    }
                    p{
                        font-size: .7rem;
                        text-align: center;
                        font-weight: bold;
                    }
                }
                .specialContent{
                    font-size: .8rem;
                    text-align: center;
                }
                &:nth-child(1){
                    .specialTitle{
                        box-shadow: 0 0 10px rgba(#0D7C66, .5);
                        svg{
                            color: #0D7C66;
                        }
                    }
                }
                &:nth-child(2){
                    .specialTitle{
                        box-shadow: 0 0 10px rgba(#1E2A5E, .5);
                        svg{
                            color: #1E2A5E;
                        }
                    }
                }
                &:nth-child(3){
                    .specialTitle{
                        box-shadow: 0 0 10px rgba(#FF204E, .5);
                    }
                }
                &:nth-child(4){
                    .specialTitle{
                        box-shadow: 0 0 10px rgba(#FFBF00, .5);
                        svg{
                            color: #FFBF00;
                        }
                    }
                }
            }
        }
    }
    .CV-content-three{
        width: 100%;
        padding-top: 17vh;
        padding-bottom: 10vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        .CV-title-three{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 80px;
            opacity: 0;
            transform: translateY(20%);
            transition: all 1s ease-in-out;
            &.in-view{
                opacity: 1;
                transform: translateY(0);
            }
            h1 {
                font-size: 2.5rem;
                color: #333;
                text-align: center;
                width: 70%;
                position: relative;
                margin-bottom: 0;
                &::after {
                    content: "";
                    width: 100px;
                    height: 7px;
                    background-color: #FFBF00;
                    position: absolute;
                    bottom: -10px;
                    right: 50%;
                    transform: translateX(50%);
                }
            }
        }   
        .CV-description-three{
            width: 100%;
        }     
    }
    .CV-content-four{
        font-size: 1.2rem;
        text-align: center;
        padding: 3em 0;
    }
}