@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15px);
    }
}

.SuccessViral-container{
    font-family: "GmarketSansMedium";
    width: 100%;
    .down-btn{
        color: #1E2A5E;
        position: absolute;
        bottom: 3%;
        transform: translateX(-50%);
        cursor: pointer;
        animation: bounce 2s infinite;
        svg{
            font-size: 40px;
        }
    }
    .SuccessViral-title{
        height: 100vh;
        width: 100%;
        background-image: url(../../../public/images/success-back.png);
        background-size: cover;
        background-position: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 35vh;
        h1{
            font-size: 2.5rem;
            text-align: center;
            width: 70%;
            position: relative;
            margin: 0 auto;
            margin-bottom: 100px;
            opacity: 0;
            transition: all 1s ease-in-out;
            &.in-view{
                opacity: 1;
            }
            &::after{
                content: "";
                width: 500px;
                height: 7px;
                background-color: #1E2A5E;
                position: absolute;
                bottom: -20px;
                right: 50%;
                transform: translateX(50%);
            }
        }
        h3, h5{
            text-align: center;
            margin: 0 auto;
            margin-bottom: 50px;
            color: #333;
            opacity: 0;
            padding: 10px 30px 10px 30px;
            box-shadow: 0px 0px 10px rgba(0, 0, 0, .2);
            border-radius: 20px;
            background-color: rgba(255, 255, 255, 0.1); 
            backdrop-filter: blur(10px); 
        }
        h3{
            font-size: 1.8rem;
            transition: all 1s ease-in-out;
            transition-delay: 1s;
            transform: translateX(-50px);
            &.in-view{
                opacity: 1;
                transform: translateX(0);
            }
        }
        h5 {
            font-size: 1.5rem;
            transition: all 1s ease-in-out;
            transition-delay: 2s;
            transform: translateX(50px);
            &.in-view{
                opacity: 1;
                transform: translateX(0);
            }
        }
    }
    .SuccessViral-content-one{
        background-color: #F5F7F8;
        position: relative;
        padding-bottom: 10vh;
        .SuccessTab{
            position: absolute;
            width: 100%;
            height: 100px;
            margin-top: 9vh;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
            background: #FFF;
            a{
                font-size: 1rem;
                color: #333;
                cursor: pointer;
                text-decoration: none;
                &.selectTab{
                    color: #1E2A5E;
                    font-weight: bold;
                    font-size: 1.3rem;
                    cursor: default;
                }
            }
        }
    }
    .Success-content-two{
        padding: 15vh 0;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        background-image: url(../../../public/images/success-back-two.png);
        background-size: cover;
        background-position: center;
        .Success-Performance{
            display: flex;
            flex-direction: column;
            align-items: center;
            h3{
                font-size: 2rem;
                color: #FFF;
                margin-bottom: 150px;
                padding: 20px 40px 20px 40px;
                border-radius: 100px;
                background-color: rgba(#1E2A5E, .8);
                backdrop-filter: blur(20px);
                transform: translateY(50px);
                opacity: 0;
                transition: all 2s ease-in-out;
                &:last-child{
                    margin-bottom: 0;
                }
                &.in-view{
                    opacity: 1 !important;
                    transform: translateY(0) !important;
                }
            }
        }
    }
    .Success-content-three{
        width: 100%;
        padding: 10vh 0;
        background-color: #F5F7F8;
        .Success-FAQ{   
            width: 100%;
            h1{
                font-size: 2.5rem;
                text-align: center;
                width: 70%;
                position: relative;
                margin: 0 auto;
                margin-bottom: 100px;
                opacity: 0;
                transform: translateY(50px);
                transition: all 1s ease-in-out;
                &.in-view{
                    opacity: 1;
                    transform: translateY(0);
                }
                &::after{
                    content: "";
                    width: 100px;
                    height: 7px;
                    background-color: #1E2A5E;
                    position: absolute;
                    bottom: -20px;
                    right: 50%;
                    transform: translateX(50%);
                }
            }
            .FAQ-card{
                width: 60%;
                margin: 0 auto;
                padding: 30px;
                display: flex;
                flex-direction: column;
                align-items: center;
                border-radius: 35px;
                box-shadow: 0 0 20px rgba(#1E2A5E, 0.5);
                background-color: #FFF;
                margin-bottom: 50px;
                opacity: 0;
                transform: translateY(100px);
                transition: all .5s ease-in-out;
                &.in-view{
                    opacity: 1;
                    transform: translateY(0);
                }
                &:last-child{
                    margin-bottom: 0;
                }
                h3{
                    width: 100%;
                    font-size: 1.7rem;
                    padding-bottom: 15px;
                    margin-bottom: 15px;
                    border-bottom: 1px solid #333;
                    letter-spacing: 2px;
                }
                p{
                    width: 100%;
                    font-size: 1rem;
                    text-align: left;
                    padding-bottom: 15px;
                    letter-spacing: 1px;
                }
            }
        }
    }
}